import React, { useEffect } from "react";

import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image"
import { Helmet } from 'react-helmet';

import Sidebar from "./sidebar";

export default ({ children, location }) => {
  const data = useStaticQuery(graphql`
    query {
      viber: file(relativePath: { eq: "viber.png" }) {
        childImageSharp {
          fixed(width: 63, height: 63){
            ...GatsbyImageSharpFixed
          }
        }
      }
      telegram: file(relativePath: { eq: "telegram.png" }) {
        childImageSharp {
          fixed(width: 55, height: 55){
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `);

  useEffect(() => {
    setTimeout(function() {
      /* eslint-disable */
      !function(f,b,e,v,n,t,s)
      {if(f.fbq)return;n=f.fbq=function(){n.callMethod?
      n.callMethod.apply(n,arguments):n.queue.push(arguments)};
      if(!f._fbq)f._fbq=n;n.push=n;n.loaded=!0;n.version='2.0';
      n.queue=[];t=b.createElement(e);t.async=!0;
      t.src=v;s=b.getElementsByTagName(e)[0];
      s.parentNode.insertBefore(t,s)}(window,document,'script',
      'https://connect.facebook.net/en_US/fbevents.js');
      fbq('init', '360882127934007');
      fbq('track', 'PageView');
      /* eslint-enable */
    }, 2000);
  });

  return (
    <>
      <Helmet defer={false}>
        <title>Фотограф Минск</title>
        <meta property='og:image' content="/logo-full.jpg" />
        <meta property='og:image:type' content="image/jpg" />
        <meta property="og:image:width" content="636" />
        <meta property="og:image:height" content="227" />
      </Helmet>
      <div className="contact-links">
        <a href="viber://chat?number=%2B375336651916" style={{ marginRight: '5px' }}>
          <Img fixed={data.viber.childImageSharp.fixed} alt="Viber" />
        </a>
        <a href="tg://resolve?domain=olga_emelyantsava">
          <Img fixed={data.telegram.childImageSharp.fixed} alt="Telegram" />
        </a>
      </div>
      <div className="wrap">
        <Sidebar location={location} />
        <main className="content {location.pathname === '/' ? 'home-page' : ''}">
          {children}
        </main>
      </div>
    </>
  );
};
